/* Existing styles */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New styles for layout and component styling */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

.App-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.CitySearch {
  margin-bottom: 20px;
  position: relative;
}

.CitySearch input.city {
  padding: 8px;
  width: 200px; /* Adjusted width */
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.CitySearch ul.suggestions {
  list-style-type: none;
  padding: 0;
  margin: 0;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: absolute;
  width: calc(100% - 20px);
  top: 45px;
  left: 10px;
  z-index: 1000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.CitySearch ul.suggestions li {
  padding: 10px;
  cursor: pointer;
}

.CitySearch ul.suggestions li:hover {
  background: #f0f0f0;
}

.NumberOfEvents {
  margin-bottom: 20px;
}

.NumberOfEvents input {
  padding: 8px;
  width: 80px; /* Adjusted width */
  border: 1px solid #ccc;
  border-radius: 4px;
}

.events-list {
  padding: 0;
  list-style: none;
}

.event-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.event-item:last-child {
  border-bottom: none;
}

.event {
  text-align: left;
  flex-grow: 1;
}

.event h2 {
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: #333;
}

.event p {
  margin: 5px 0;
  color: #555;
  font-size: 0.9em;
}

input[type="text"],
input[type="number"] {
  padding: 8px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px;
  margin-top: 10px;
  background-color: #282c34;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #61dafb;
}

button.show-details {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px;
}

button.show-details:hover {
  background-color: #0056b3;
}
